import { useDispatch } from 'react-redux';
import { useState } from 'react';

import { signup } from '../../redux/auth/authOperations';
import { FiEye, FiEyeOff } from 'react-icons/fi';
import toast from 'react-hot-toast';

import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import { createTheme, ThemeProvider } from '@mui/material/styles';

const theme = createTheme();

export const RegisterForm = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);

  const dispatch = useDispatch();

  const handleChange = () => {
    if (showPassword) {
      return setShowPassword(false);
    }
    setShowPassword(true);
  };

  const handleSubmit = async e => {
    e.preventDefault();
    const status = await dispatch(
      signup({
        name,
        email,
        password,
      })
    );

    if (status.error && name && password && email) {
      return toast(`Please, enter other data for registration`, {
        style: { color: '#1976d2' },
      });
    }

    if (status.meta.requestStatus === 'fulfilled') {
      toast(`Registration success`, { style: { color: 'green' } });
      setEmail('');
      setName('');
      setPassword('');
    }
  };
  return (
    <ThemeProvider theme={theme}>
      <Grid container component="main" sx={{ height: '90vh' }}>
        <CssBaseline />
        <Grid
          item
          xs={12}
          sx={{
            marginBottom: 5,
            borderTopLeftRadius: 16,
            borderBottomLeftRadius: 16,
          }}
          sm={8}
          md={5}
          component={Paper}
          elevation={6}
          square
        >
          <Box
            sx={{
              my: 8,
              mx: 4,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
            }}
          >
            <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}></Avatar>
            <Typography component="h1" variant="h5">
              Sign up
            </Typography>
            <Box
              component="form"
              noValidate
              onSubmit={handleSubmit}
              sx={{ mt: 1 }}
              position="relative"
            >
              <TextField
                margin="normal"
                autoComplete="given-name"
                name="name"
                value={name}
                onChange={e => setName(e.target.value)}
                required
                fullWidth
                id="name"
                label="Name"
                autoFocus
              />

              <TextField
                margin="normal"
                name="email"
                value={email}
                onChange={e => setEmail(e.target.value)}
                required
                fullWidth
                id="email"
                label="Email Address"
                autoComplete="email"
              />
              <TextField
                margin="normal"
                required
                fullWidth
                name="password"
                onChange={e => setPassword(e.target.value)}
                value={password}
                label="Password"
                type={!showPassword ? 'password' : 'text'}
                id="password"
                autoComplete="new-password"
              />
              {!showPassword ? (
                <FiEyeOff
                  style={{
                    position: 'absolute',
                    bottom: 104,
                    right: 15,
                  }}
                  onClick={handleChange}
                />
              ) : (
                <FiEye
                  style={{ position: 'absolute', bottom: 104, right: 15 }}
                  onClick={handleChange}
                />
              )}
              <Button
                type="submit"
                fullWidth
                variant="contained"
                sx={{ mt: 3, mb: 2 }}
              >
                Sign Up
              </Button>
            </Box>
          </Box>
        </Grid>
        <Grid
          item
          xs={false}
          sm={4}
          md={7}
          sx={{
            backgroundImage: 'url(https://source.unsplash.com/random)',
            backgroundRepeat: 'no-repeat',
            backgroundColor: t =>
              t.palette.mode === 'light'
                ? t.palette.grey[50]
                : t.palette.grey[900],
            backgroundSize: 'cover',
            backgroundPosition: 'center',
            borderTopRightRadius: 16,
            borderBottomRightRadius: 16,
            marginBottom: 5,
          }}
        />
      </Grid>
    </ThemeProvider>
  );
};
